.access {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.95);
  z-index: 9900;
  padding: 70px;
  box-sizing: border-box;

  .close_access {
    position: absolute;
    top: 5%;
    right: 5%;
    cursor: pointer;
  }

  .access_wrap {
    display: flex;
    flex-direction: row;
    align-items: flex-start;

    .access_left {
      width: 40%;
      margin-right: 10%;

      h3 {
        font-family: SCDream5;
        color: #fff;
        font-size: 2.5rem;
        padding-bottom: 10px;
        margin-bottom: 10px;
        word-break: keep-all;
        border-bottom: 1px solid #fff;
        ;
      }

      span {
        font-family: SCDream2;
        display: block;
        color: #fff;
        font-size: 1.25rem;
        margin-bottom: 4vh;
        line-height: 1.5;
        word-break: keep-all;
      }

      ul {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 100%;

        li {
          display: flex;
          justify-content: center;
          width: 70%;
          margin-bottom: 50px;



          img {
            width: 100%;
          }
        }
      }

    }

    .access_right {
      width: 40%;

      h3 {
        color: #fff;
        font-size: 2.5rem;
        padding-bottom: 10px;
        margin-bottom: 10px;
        word-break: keep-all;
        border-bottom: 1px solid #fff;
        ;
      }

      span {
        display: block;
        color: #fff;
        font-size: 1.25rem;
        margin-bottom: 4vh;
        line-height: 1.5;
        word-break: keep-all;
      }


      ul {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 100%;

        li {
          display: flex;
          justify-content: center;
          width: 100%;

          img {
            width: 100%;
          }
        }
      }

    }

  }
}

@media screen and (max-width:845px) {
  .access {
    padding: 50px;
    overflow: auto;

    .close_access {
      top: 30px;
      right: 30px;
      width: 24px;
      height: 24px;

      img {
        width: 100%;
      }

    }

    .access_wrap {
      flex-direction: column;

      .access_left {
        width: 100%;
        margin-bottom: 4vh;
        margin-right: 0%;

        h3 {
          font-size: 2rem;
        }

        span {
          font-size: 1rem;
        }

        ul {
          li {
            margin-bottom: 30px;
          }
        }

      }

      .access_right {
        width: 100%;

        h3 {
          font-size: 2rem;
        }

        span {
          font-size: 1rem;
        }

      }

    }
  }

}