.App {
    width: 100%;
    height: 100%;
    overflow: hidden;
    background: linear-gradient(-45deg, #1e272e, #485460, #828282, #485460, #1e272e);
    background-size: 400% 400%;
    animation: gradient 8s ease infinite;
    color: #fff;
}


::selection {
    background-color: #ffa801;
    color: #000;
}

@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }

    50% {
        background-position: 100% 50%;
    }

    100% {
        background-position: 0% 50%;
    }
}